import React, { FC } from 'react';
import '../styles/post.css';
import Layout from '../components/layout';
import { TPost } from '../types/data';
import SEO from '../components/seo';

type Props = {
  pageContext: {
    post: TPost;
  };
};

const Post: FC<Props> = ({ pageContext }) => {
  const { title, updatedAt, image } = pageContext.post.node;
  const { description } = pageContext.post.node.description;
  const body = pageContext.post.node.body.childMarkdownRemark.html;

  return (
    <Layout>
      <SEO title={title} description={description} />
      <div className="post-header">
        <h1>{title}</h1>
        <p className="post-date">{updatedAt}</p>
      </div>
      <img src={image.file.url} className="post-image" alt="post-cover" />
      <div dangerouslySetInnerHTML={{ __html: body }} className="post-body" />
    </Layout>
  );
};

export default Post;
